import React from 'react';

import './skill.scss';

const Skill = ({ name, progress, activelyLearning }) => {

    const getStyles = () => ({
        strokeDashoffset: (1 + progress / 100) * 370,
        transform: `rotate(${-(360 - (progress * 360 / 100)) - 90}deg)`
    });

    return (
        <div className="skill">
            {activelyLearning && (
                <div className="actively-learning">
                    Actively Learning
                </div>
            )}
            <h4>{name}</h4>
            <svg className="arc" viewBox="0 0 112 112" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
                <ellipse className="circle" ry="59" rx="59" cy="56" cx="56" strokeWidth="6" style={getStyles()} />
            </svg>
        </div>
    );
}

export default Skill;