import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import facebook from '../resources/images/facebook.svg';
import linked_in from '../resources/images/linkedin.svg';

import './footer.scss';

const Footer = () => (
    <footer className="jumbotron">
        <Container>
            <div className="social">
                <a href="https://www.facebook.com/sergeyakzhik" target="_blank" rel="noopener noreferrer">
                    <img src={facebook} alt="Facebook"/>
                </a>
                <a href="https://www.linkedin.com/in/sergei-yakzhyk-4aab1617a/" target="_blank" rel="noopener noreferrer">
                    <img src={linked_in} alt="LinkedIn"/>
                </a>
            </div>
        </Container>
    </footer>
);

export default Footer;