import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import photo from '../resources/images/avatar.jpg';

import './header.scss';
 
const Header = () => (
    <header>
        <Container>
            <Row className="first-row">
                <Col xs={4} sm={{ span: 3, offset: 2 }}>
                    <div className="author-image">
                        <img src={photo} alt="author" />
                    </div>
                </Col>
                <Col xs={8} sm={6} className="align-self-center">
                    <span className="name">SERGEI</span>
                    <br/>
                    <span className="surname">YAKZHYK</span>
                </Col>
            </Row>
            <Row className="justify-content-center">
                <Col xs={6} sm={3}>
                    <h4>Location</h4>
                    <h5>Minsk, Belarus</h5>
                </Col>
                <Col xs={6} sm={3}>
                    <h4>Telephone Number</h4>
                    <a href="tel:+375445593051">
                        <h5>+375 (44) 559-30-51</h5>
                    </a>
                </Col>
                <Col xs={6} sm={3}>
                    <h4>Email</h4>
                    <a href="mailto:sergeyakzhik@gmail.com">
                        <h5>sergeyakzhik@gmail.com</h5>
                    </a>
                </Col>
            </Row>
        </Container>
    </header>
);

export default Header;